import api from "../../common/api.js";
var _self;
export default {
    data() {
        return {
            classData: [],
            videoData: [],
			classId: 0,
			className: 'class',
			tagId: 0,
			tagName: 'tag',
			areaId: 0,
			areaName: 'area',
			orderCode: 'lastTime',
			countArr: 0,
			where: '',
			isSel: 0,
			isShowTig: false,
			page: 1,
			more: false,
			isMoreTig: false
        }
    },
	onLoad() {
		_self = this;
		// 加载分类数据
		_self.getClassData();
		// 加载分类视频
		_self.getVideoData();
	},
    methods: {
		// 跳转播放页
		playVideo(vid){
			_self.notData("加载中", 1000);
			//console.log(vid);
			uni.navigateTo({
				url:"/pages/video/play?vid="+vid
			});
		},
		// 分类数据
		getClassData(){
			uni.request({
				url: api.apiData.classData,
				method: 'GET',
				success: (e) => {
					// 分类数据
					_self.classData = e.data.Data;
					// 隐藏加载状态
					uni.stopPullDownRefresh();
				},
				fail: () => {
					_self.notData("网络连接失败，请检查网络是否正常");
				}
			});
		},
		// 分类视频
		getVideoData(){
			uni.request({
				url: api.apiData.videoData+_self.where,
				method: 'GET',
				success: (e) => {
					// 非加载更多状态,清空数组
					if(!_self.more) _self.videoData = [];
					// 我是有底线的
					if(e.data.Data.videolist.length>0){
						_self.isMoreTig = false;
						// 分类视频
						_self.videoData = _self.videoData.concat(e.data.Data.videolist);
					}else{
						_self.isMoreTig = true;
						_self.page = _self.page-1;
					}
					// 无数据
					if(_self.videoData.length>0){
						_self.isShowTig = false;
					}else{
						_self.isMoreTig = false;
						_self.isShowTig = true;
					}
					// 隐藏加载状态
					uni.stopPullDownRefresh();
				},
				fail: () => {
					// 加载失败提示
					_self.notData("网络连接失败，请检查网络是否正常");
				}
			});
		},
		// 点击分类
		classSel(type, selId, more = 0){
			uni.startPullDownRefresh();
			if(more){
				_self.more = true;
			}else{
				_self.more = false;
				_self.page = 1;
			}
			switch(type) {
			    case 'class':
			        _self.classId = selId;
			        break;
			    case 'tag':
			        _self.tagId = selId;
			        break;	
				case 'area':
				    _self.areaId = selId;
				   break;
			    default:
					_self.orderCode = selId;
			} 
			_self.where = '?page='+_self.page+'&cid='+_self.classId+'&tag_id='+_self.tagId+'&area_id='+_self.areaId+'&orderCode='+_self.orderCode;
			_self.getVideoData();
		},
		// 提示
		notData(title, times = 3000){
			// 加载失败提示
			uni.showToast({
				icon: "none",
				title: title,
				mask: true,
				duration: times
			});
		}
    },
	// 下拉刷新
	onPullDownRefresh(){
		_self.getClassData();
	},
	// 上拉加载数据
	onReachBottom(){
		_self.page++;
		_self.classSel('orderCode', _self.orderCode, 1);
		console.log(_self.page);
	}
}
